
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import scroll from "@/components/scroll/scroll";
import slide from "@/components/Slide/slide";
import Collapse from "@/components/Collapse/Collapse";
import backTop from "@/components/BackTop/backTop";
import { imgPreview } from "@/assets/js/img.js";
import BScroll from "better-scroll";
export default {
components: {
XHeader,
Collapse,
backTop,
Tab,
TabItem,
InlineLoading,
slide,
XTextarea,
XInput
},
data() {
return {
screenHeightNoChange: true,
tabIndex: 0,
classify1: [], // 一级分类
classify2: {"1": [{"name": "xx"}]}, // 二级分类
type: [], // 反馈分类
typeIndex: -1,
content: "",
phoneNum: "",
img: [],
nowtransform:'',//暂存滚动高度字符
tab2:{},//tab2的滚动
};
},
mounted() {

const self = this;
window.onresize = () => {
if (self.oldFullHeight) {
self.screenHeightNoChange = document.documentElement.clientHeight === self.oldFullHeight;
console.log(' self.screenHeightNoChange ' + self.screenHeightNoChange);
}
};
this.tabScroll(this.$refs.tab1);
this.tab22=new BScroll(this.$refs.tab2);
},
beforeMount() {

this.getClassifyFirst();

this.getFeedbackType();
},
methods: {

getClassifyFirst() {
this.$ajax.post(this.$root.host,{
"MSH.1": "commonProblem",
"MSH.2": "getAvailableProblemTypeList",
"token": this.$root.loginInfo.token,
"name": "",
"pageIndex": 1,
"pageSize": 10000,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
if (typeof res != 'undefined' && typeof res.rows != 'undefined') {
this.classify1 = res.rows;
for (var index = 0; index < res.rows.length; index++) {
const element = res.rows[index];
this.getClassifySecond(element.CBM);
}
}
}).catch(error => {
console.log(error);
})
},

getClassifySecond(CBM) {
this.$ajax.post(this.$root.host,{
"MSH.1": "commonProblem",
"MSH.2": "getAvailableProblemList",
"token": this.$root.loginInfo.token,
"title": "",
"typeCbm": CBM,
"pageIndex": 1,
"pageSize": 10000,
'token':this.$root.loginInfo.token,
}).then(res => {
if (typeof res != 'undefined' && typeof res.rows != 'undefined') {
this.classify2[CBM] = res.rows;
}
}).catch(error => {
console.log(error);
})
},

gotoDetail(CBM) {
this.$router.push({name: "helpDetails", params: {CBM: CBM}});
},

getFeedbackType() {
this.$ajax.post(this.$root.host,{
"MSH.1": "commonProblem",
"MSH.2": "fklx",
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
if (typeof res != 'undefined') {
this.type = res;
}
}).catch(error => {
})
},

postProblem() {
this.$ajax.post(this.$root.host,{
"MSH.1": "commonProblem",
"MSH.2": "postProblem",
"CHZBM": this.$root.loginInfo.CHZBM,
"typeCbms": (this.type[this.typeIndex]).ZDKEY,
"content": this.content,
"link": this.phoneNum,
"picPaths": this.img.toString(","),
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
let THIS = this;
if (typeof res !='undefined' && typeof res.status !='undefined' && res.status == true) {
this.$vux.toast.show({
text: "提交成功",
type: "success",
onHide() {
THIS.$router.goBack();
}
});
} else {
this.$vux.toast.text("提交失败", "bottom");
}
}).catch(error => {
console.log(error);
})
},
tabScroll(e) {
new BScroll(e);
},
slide(index) {
this.$refs.slide.goToPage(index);
},

getImage() {
var json = "{type:'2',photoType:'4'}";
if (this.$root.phoneType == "ios") {
window.webkit.messageHandlers.mutual.postMessage({type: "2", photoType: "4"});
} else {
android: AndroidFun.mutual(json);
}

let self = this;
imgPreview(files[0], function(src) {
self.img.push(src);
});
},

setTop(refname){

this.nowtransform= this.tab22.scroller.style.transform;
let toppx=0;
if(this.$refs[refname].$el){
toppx=this.$refs[refname].$el.parentElement.parentElement.offsetTop-this.$refs.tab2.children[0].offsetHeight/5;
}else{
toppx=this.$refs[refname].parentElement.parentElement.offsetTop-this.$refs.tab2.children[0].offsetHeight/5;
}
if(toppx>Math.abs(parseInt(this.nowtransform.split('scale')[0].split(',')[1]))&&this.$root.phoneType == "android"){
this.$refs.tab2.children[0].style.transform='translate(0px, -'+toppx+'px) scale(1) translateZ(0px)'
}
},

backNormalHeight(){
if(this.$root.phoneType == "android"){
this.tab22.scroller.style.transform=this.nowtransform
}
}
},
watch: {
"$root.feedbackImage"(newValue) {
this.img.push(this.$root.hostImage + newValue);
}
}
};



